@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;1,200;1,300;1,500;1,600;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
    background-color: #F8F1F1;
    font-family: 'Poppins', sans-serif;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


.square{
    display: inline-block;
    height: 45vh;
    width: 45vh;
    margin: 0 10px;
    overflow: hidden;
    border-radius: 16px;
    border: 1px solid #fff;
    background-color: #fff;
    box-shadow: inset 4px 4px 3px 1px rgba(97,97,97,0.82);
}



.square ul{
  list-style-type: none;
    padding: 16px;
}


.container{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
    height: calc(100vh - 20px);
    width: 80%;
    margin: 0 auto;
    padding: 10px;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    position: relative;
    box-shadow: 4px 4px 3px 1px rgba(196,196,196,0.82);
}

.container:after{
  content: "Presentación";
    font-size: 70px;
    font-weight: bold;
    position: absolute;
    bottom: 7px;
    left: 0;
    color: #d98b8b;
}
.container:before{
  content: "Ariel Merino";
    font-size: 70px;
    font-weight: bold;
    position: absolute;
    bottom: 70px;
    left: 0;
}
